import { RESET_USER_PAGE_STATUS, SET_INIT_DATA, SET_USER_LOGGED_IN, SET_USER_PAGE_STATUS, } from "../constants";
import { initialState, } from "../providers/UserPageAppContextProvider";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_USER_PAGE_STATUS: {
            const { isActiveUser, setupCompleted, userId, userName, isQuicktaskerUser, isWordPressUser, userType, profilePictureUrl, } = action.payload;
            return Object.assign(Object.assign({}, state), { isActiveUser: Boolean(isActiveUser), setupCompleted, initialLoading: false, userId,
                userName,
                isQuicktaskerUser,
                isWordPressUser,
                userType,
                profilePictureUrl });
        }
        case SET_USER_LOGGED_IN: {
            const isLoggedIn = action.payload;
            return Object.assign(Object.assign({}, state), { isLoggedIn });
        }
        case SET_INIT_DATA: {
            const { timezone } = action.payload;
            return Object.assign(Object.assign({}, state), { timezone });
        }
        case RESET_USER_PAGE_STATUS: {
            return Object.assign({}, initialState);
        }
        default:
            return state;
    }
};
export { reducer };
